import React, { Component } from 'react'
import { NO_PRODUCT } from '../../../constants'

class ContinueShop extends Component {

    render() {
        const { productDetails, closeAddtocart, productImg, tagshopProductDeatil, onBack, price_currency_symbol, product_discount, product_price, discount, quantity, selectedAddToCart } = this.props
        return <div className='tb_cart_container'>
            <div className="tb_cart_back_btn" onClick={onBack}>
                <div className='tb__icon tb-arrow-left'><div></div></div>
                Back
            </div>
            <div className="tb_cart_details">
                <div className="tb_cart_p_img">
                    <img src={productImg} alt="" onError={e => e.target.src = NO_PRODUCT} height={400} width={300} />
                </div>
                <div className='tb_cart_p_details'>
                    <div className='tb_card_de_in'>
                        <div className="tb_cart_p_title">{selectedAddToCart?.name || productDetails?.name || tagshopProductDeatil.title}</div>
                        <div className='tb_cart_pp_wrap'>
                            <div className="tb_cart_p_price">{price_currency_symbol}{selectedAddToCart?.price || (product_discount > 0 ? product_discount : product_price)}</div>
                            {selectedAddToCart?.price == 0 && (product_discount > 0 && product_discount != product_price) ? <div className="tb_cart_p_dis">{price_currency_symbol}{product_price}</div> : null}
                            {discount > 0 ? <div className="tb_cart_p_dp">{`${discount}%`} off</div> : null}
                        </div>
                        {tagshopProductDeatil?.sku?.length > 1 ? <div className="tb_cart_sku">SKU : {selectedAddToCart?.sku || tagshopProductDeatil.sku}</div> : null}

                        {quantity ? <div className="tb_cart_p_total">
                            Your cart now has {quantity} item
                        </div> : <></>}

                    </div>
                    <div className="tb_cart_actions tb_cart_c_action">
                        <div className="tb_cart_primary" onClick={() => window.location.href = `${window.location.origin}/checkout/cart/`}>Proceed to checkout</div>
                        <div className="tb_cart_secondary" onClick={closeAddtocart}>Continue Shopping</div>
                    </div>
                </div>
            </div>
        </div>
    }
}
export default ContinueShop