import React, { Component } from 'react'
import axios from 'axios';
import { PostTrimContent } from '../../../utils'
import { NO_PRODUCT } from '../../../constants'
import MagentoShop from './MagentoShop'
import "./scss/CartPopUp.scss"
import parse from "html-react-parser";
import Dropdown from './Dropdown'

const trim_content_length = 200

function getCookie(key) {
       const cookies = document.cookie.split("; ");
       for (let cookie of cookies) {
              const [cookieKey, cookieValue] = cookie.split("=");
              if (cookieKey === key) {
                     return decodeURIComponent(cookieValue);
              }
       }
       return null; // Return null if the key is not found
}

async function addMagentoToCart(productSku, qty = 1, cartId = null, token = null, callback) {
       const baseUrl = `${window.location.origin}/rest`;
       const endpoint = token
              ? `${baseUrl}/V1/carts/mine/items`
              : `${baseUrl}/V1/guest-carts/${cartId}/items`;

       const headers = token ? { Authorization: `Bearer ${token}` } : {};

       const data = {
              cartItem: {
                     sku: productSku,
                     qty: qty,
                     quote_id: cartId,
              },
       };

       try {
              await axios.post(endpoint, data, { headers });
              loadCustomerSection(['cart', 'directory-data', 'messages'], callback);

       } catch (error) {
              console.error('Error adding to cart:', error.response?.data || error.message);
       }
}

async function loadCustomerSection(sections = ['cart'], callback) {
       const sectionQuery = sections.join(',');
       try {
              await axios.get(`${window.location.origin}/customer/section/load/`, {
                     params: {
                            sections: sectionQuery,
                            force_new_section_timestamp: true,
                            _: Date.now(), // Current timestamp to prevent caching
                     },
                     withCredentials: true,
              });
              callback()

       } catch (error) {
              console.error('Error loading sections:', error.response?.data || error.message);
       }
}


class AddToCarts extends Component {
       constructor(props) {
              super(props);
              const isShow = props?.productDetails?.data?.description?.length >= trim_content_length
              this.state = {
                     allSizes: null, allColors: null, selectedColor: null, selectedSize: null, availableSize: [], productImage: '', optionError: false, continueShopping: false, selectedVariant: null,
                     productOption: [], isSoldOut: false,
                     quantity: 1,
                     isExpanded: isShow ? true : false,
                     isReadMore: isShow,
                     variantImages: [],
                     selectedVarientData: {},
                     selectedAddToCart: {},
                     isRequesting: false,
                     productError: false
              };
       }

       updateQuantity = (increase) => event => {
              this.setState(prevState => ({
                     quantity: increase ? prevState.quantity + 1 : prevState.quantity > 1 ? prevState.quantity - 1 : 1,
              }));
       }


       onAddToCart = () => {
              try {
                     const { selectedVarientData } = this.state
                     const { productDetails } = this.props
                     if (((selectedVarientData && Object.keys(selectedVarientData).length) || productDetails?.data.productVariants.length == 0) && productDetails?.data?.attributes.length == Object.keys(selectedVarientData).length) {
                            if (productDetails && Object.keys(productDetails).length) {
                                   this.setState({ optionError: false, productError: false })
                                   this.addProductsToCart()
                            }
                     }
                     else {
                            this.setState({ optionError: true, productError: false })
                     }
              } catch (error) {
                     console.log('error', error)
                     this.setState({ optionError: false, productError: false })
              }
       }

       codeCheck = async (product_sku, callback) => {
              const { quantity } = this.state
              const guestCartId = await axios.post(`${window.location.origin}/rest/V1/guest-carts`).then(res => res.data);
              addMagentoToCart(product_sku, quantity, guestCartId, null, callback);
       }
       addProductsToCart = () => {
              const { tagshopProductDeatil, productDetails } = this.props
              const { quantity,selectedVarientData } = this.state
              const productURL = tagshopProductDeatil.UgcProduct.product_url
              const convertURL = btoa(productURL)
              const myCookieValue = getCookie("form_key");
              this.setState({ isRequesting: true, productError: false })
              const cartURL = `${window.location.origin}/checkout/cart/add/uenc/${convertURL}/product/${tagshopProductDeatil.UgcProduct.shopify_product_id}/`
              const formData = new FormData();
              formData.append('product', tagshopProductDeatil.UgcProduct.shopify_product_id);
              formData.append('selected_configurable_option', '');
              formData.append('related_product', '');
              formData.append('item', tagshopProductDeatil.UgcProduct.shopify_product_id);
              formData.append('form_key', myCookieValue);
              formData.append('qty', quantity);

              if (selectedVarientData && Object.keys(selectedVarientData).length) {
                     Object.keys(selectedVarientData).map((key, index) => {
                            const varientValue = productDetails.data.attributes.filter((attr) => attr.label == key)
                            if (varientValue && varientValue.length > 0) {
                                   if (varientValue[0] && varientValue[0].values.length > 0) {
                                          const varientId = varientValue[0].values.filter((val) => val.label == selectedVarientData[key])
                                          if (varientId && varientId.length > 0) {
                                                 formData.append(`super_attribute[${varientValue[0].id}]`, varientId[0].value_index)
                                          }
                                   }
                              }
                         })
                     }


              axios.post(cartURL, formData, {
                     headers: {
                            'Content-Type': 'multipart/form-data'
                     }
              })
                     .then(response => {
                            this.setState({ continueShopping: true, isRequesting: false, productError: false })
                     })
                     .catch(error => {
                            console.error('Error:', error);
                            this.setState({ isRequesting: false, productError: true })

                     });

       }

       toggleExpand = event => {
              const { isExpanded } = this.state;
              this.setState({ isExpanded: !isExpanded })
       }
       handleDropdownChange = (value, label, type) => {
              const { selectedVarientData } = this.state
              selectedVarientData[type] = label
              this.setState({ selectedVarientData }, () => this.onSelecteProduct())
       };
       onSelecteProduct = () => {
              const { selectedVarientData } = this.state
              const { productDetails } = this.props
              if ( Object.keys(selectedVarientData || {}).length && productDetails?.data?.attributes.length == Object.keys(selectedVarientData).length) {
                     const searchTerms = Object.values(selectedVarientData);
                     const matchingProducts = productDetails.data.productVariants.filter(product =>
                            searchTerms.every(term => product.sku.includes(term))
                     );
                     this.setState({ selectedAddToCart: matchingProducts[0] })
              }

       }

       render() {
              const { closeCart, productDetails, productImg, tagshopProductDeatil, closePopUp } = this.props
              const {optionError, productError, continueShopping, selectedVariant, productOption, isSoldOut, quantity, isExpanded, isReadMore, selectedAddToCart, isRequesting } = this.state
              const { product_price, product_discount, price_currency_symbol, product_title } = tagshopProductDeatil?.UgcProduct
              const discount = product_discount > 0 && product_discount != product_price ? (((parseInt(product_price) - parseInt(product_discount)) / parseInt(product_price)) * 100).toFixed() : 0
              const postTextContent = isExpanded ? PostTrimContent(productDetails.data.description, trim_content_length) : productDetails.data.description

              return (
                     <div className="tb_cart_modal tb_modal_show" aria-hidden="true">
                            <div className="tb_cart_modal_dialog" role="document">
                                   <div className="tb_cart_modal_content">
                                          <div className="tb_c_close_btn" onClick={closePopUp}>
                                                 <div className='tb__icon tb-close-thin'><div></div></div>
                                          </div>
                                          <div className="tb_cart_modal_body">
                                                 {continueShopping ?
                                                        <MagentoShop quantity={quantity} selectedAddToCart={selectedAddToCart} productImg={productImg} productDetails={selectedVariant} tagshopProductDeatil={tagshopProductDeatil} closeAddtocart={closeCart} onBack={event => this.setState({ continueShopping: false })} price_currency_symbol={price_currency_symbol} product_discount={product_discount} product_price={product_price} discount={discount} productOption={productOption} />
                                                        :
                                                        <div className='tb_cart_container'>
                                                               <div className="tb_cart_back_btn" onClick={closeCart}>
                                                                      <div className='tb__icon tb-arrow-left'><div></div></div>
                                                                      Back
                                                               </div>
                                                               <div className="tb_cart_details">
                                                                      <div className="tb_cart_p_img">
                                                                             <img src={productImg ? productImg : NO_PRODUCT} onError={e => this.setState({ productImage: NO_PRODUCT })} height={400} width={300} alt="" />
                                                                      </div>
                                                                      <div className="tb_cart_p_details">
                                                                             <div className='tb_card_de_in'>
                                                                                    <div className='tb_cart_p_info'>
                                                                                           <div className="tb_cart_p_title">{product_title}</div>
                                                                                           <div className='tb_cart_pp_wrap'>
                                                                                                  <div className="tb_cart_p_price">{price_currency_symbol}{product_discount > 0 ? product_discount : product_price}</div>
                                                                                                  {product_discount > 0 && product_discount != product_price ? <div className="tb_cart_p_dis">{price_currency_symbol}{product_price}</div> : null}
                                                                                                  {discount > 0 ? <div className="tb_cart_p_dp">{`${discount}%`} off</div> : null}
                                                                                           </div>
                                                                                           {tagshopProductDeatil?.UgcProduct?.product_sku?.length > 1 ? <div className="tb_cart_sku">SKU : {tagshopProductDeatil.UgcProduct.product_sku}</div> : null}
                                                                                           {productDetails?.data?.inventory_quantity?.length <= 4 ? <div className="tb_cart_s_l">Only {productDetails?.data?.inventory_quantity} left in stock</div> : null}
                                                                                           {!productDetails.data.available ? <div className="tb_cart_ofs">Out of Stock</div> : null}
                                                                                           {productDetails.data.description ? <><div className="tb_cart_p">{parse(postTextContent)}</div>

                                                                                                  {isReadMore ? <div className="tb_cart_read_more_" onClick={this.toggleExpand}>{isExpanded ? "Read More..." : "Show Less..."}
                                                                                                  </div> : null}

                                                                                           </> : null}
                                                                                           {productDetails.data.vendor ? <div className="tb_cart_s_v"><div className='tb_cart_s_vt'>Vendor: </div>{productDetails.data.vendor}</div> : null}
                                                                                    </div>
                                                                                    <div className='tb_cart_var_'>
                                                                                           {
                                                                                                  productDetails?.data && Object.keys(productDetails.data)?.length ? <>
                                                                                                         {
                                                                                                                productDetails?.data?.attributes?.map((attr, index) => {
                                                                                                                       return <div className="tb_cart_size" key={index}>
                                                                                                                              <div className="tb_cart_s_label">{attr.label} <span style={{ color: "red" }}>*</span></div>
                                                                                                                              <Dropdown options={attr.values} type={attr.label} onChange={this.handleDropdownChange} />
                                                                                                                       </div>

                                                                                                                })
                                                                                                         }

                                                                                                  </> : null
                                                                                           }

                                                                                    </div>
                                                                                    <div className="tb_cart_s_label">Quantity:</div>
                                                                                    <div className='tb__qu'>
                                                                                           <div className={`tb_qu_btn${quantity <= 1 ? ' tb_disabled' : ''}`} onClick={this.updateQuantity(false)}>-</div>
                                                                                           <div className='tb_qu_input'>{quantity}</div>
                                                                                           <div className={`tb_qu_btn${quantity >= 100 ? ' tb_disabled' : ''}`} onClick={this.updateQuantity(true)}>+</div>
                                                                                    </div>
                                                                                    {optionError ? <div className="tb_cart_p_error">Please select variant to proceed!</div> : <></>}
                                                                                    {productError ? <div className="tb_cart_p_error">Product that you are trying to add is not available.</div> : <></>}
                                                                             </div>
                                                                             <div className="tb_cart_actions">
                                                                                    <div className="tb_cart_secondary" onClick={closeCart}>Cancel</div>
                                                                                    <div className={`tb_cart_primary ${isRequesting ? `tb_cart_btn_spinner` : ``}`} style={isSoldOut ? { pointerEvents: 'none', opacity: 0.4 } : {}} onClick={this.onAddToCart}>ADD TO CART</div>
                                                                             </div>
                                                                      </div>
                                                               </div>
                                                        </div>
                                                 }
                                          </div>
                                   </div>
                            </div>
                     </div >

              )
       }
}


export default AddToCarts