import React, { Component } from 'react'
import { addToCart } from '../../../actions/cartActions'
import {themePostTracking} from '../../../actions/moredataAction'
import { PostTrimContent } from '../../../utils'
import { NO_PRODUCT } from '../../../constants'
import ContinueShop from './ContinueShop'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination } from 'swiper/modules';
import '../../Themes/scss/ThemesSwiper.scss'
import "./scss/CartPopUp.scss"
import parse from "html-react-parser";


const findAInB = (A, B) => B.filter(obj => A.every(item => obj.options.includes(item)))

const trim_content_length = 200

class AddToCarts extends Component {
       constructor(props) {
              super(props);
              const isShow = props?.productDetails?.data?.description?.length >= trim_content_length
              this.state = {
                     allSizes: null, allColors: null, selectedColor: null, selectedSize: null, availableSize: [], productImage: '', optionError: false, continueShopping: false, selectedVariant: null,
                     productOption: [], isSoldOut: false,
                     quantity: 1,
                     isExpanded: isShow ? true : false,
                     isReadMore: isShow,
                     variantImages: []

              };
       }

       updateQuantity = (increase) => event => {
              this.setState(prevState => ({
                     quantity: increase ? prevState.quantity + 1 : prevState.quantity > 1 ? prevState.quantity - 1 : 1,
              }));
       }
       componentDidMount() {
              this.getOptions()
              this.getProductImage()
       }
       getOptions = () => {
              const { productDetails } = this.props
              if (productDetails && Object.keys(productDetails).length) {
                     const { data } = productDetails
                     var colors = []
                     var size = []
                     var productOption = []
                     if (data && data.options && data.options.length) {
                            data.options.map((option, index) => {
                                   if (option.values && option.values.length) {
                                          let availableVarient = []
                                          let availableVarientIndex = null
                                          let isMediaAvailable = []
                                          option.values.map((value, cIndex) => {
                                                 const result = data.variants.find(vr => vr.options.includes(value))
                                                 isMediaAvailable.push(result.featured_image?.src)

                                                 const resultCo = data.variants.filter(vr => vr.options.includes(value) && vr.available)
                                                 if (resultCo && resultCo.length > 0) {
                                                        if (result && result.available && !availableVarient.includes(value)) {
                                                               availableVarient.push(value);

                                                               if (availableVarientIndex == null && resultCo[0][`option${index + 1}`] == value) availableVarientIndex = cIndex
                                                        }
                                                 }
                                          })
                                          const filteredArray = isMediaAvailable.filter(element => element !== undefined);
                                          let currentSelectedIndex = 0
                                          if (data.options && data.options.length == 1) currentSelectedIndex = availableVarientIndex ? availableVarientIndex : 0
                                          productOption.push({ key: option.name, value: option.values, selected: option.values[index == 0 ? currentSelectedIndex : availableVarientIndex ? availableVarientIndex : 0], availableVarient: availableVarient, isMediaShow: filteredArray.length == isMediaAvailable.length })
                                   }
                            })
                     }

                     this.setState({ allSizes: size, allColors: colors, productOption }, () => this.getProductImage())
              }
       }
       onClickSelectVarient = (optionIndex, value, isMedia) =>  {

              let { productOption } = this.state
              const { productDetails } = this.props
              const { data } = productDetails
              productOption[optionIndex].selected = value
              let productImage = {}
              if (isMedia) {
                     const result = data.variants.find(vr => vr.options.includes(value))
                     productImage = { productImage: result.featured_image ? result.featured_image?.src : this.props.productImg }
              }

              let isSoldOut = false;
              var selectedOptions = [];
              productOption.map((option, index) => {
                     let availableVarient = []
                     let availableVarientIndex = null

                     if (optionIndex < index) {
                            const result = data.variants.filter(vr => vr.options.includes(value))
                            if (result && result.length > 0) {

                                   result.map((resultOpt, resultOptIndex) => {
                                          if (resultOpt.available & !availableVarient.includes(resultOpt[`option${index + 1}`])) {

                                                 availableVarient.push(resultOpt[`option${index + 1}`]);
                                                 if (availableVarientIndex == null) availableVarientIndex = resultOptIndex
                                          }
                                   })
                            }
                            productOption[index].selected = option.value[availableVarientIndex ? availableVarientIndex : 0]
                            productOption[index].availableVarient = availableVarient
                            isSoldOut = availableVarient.length == 0

                     }
                     selectedOptions.push(option.selected)

              })
              var variantImages = this.getMultiImages(data.media, selectedOptions)
              if (variantImages?.length == 0) variantImages = data.images

              this.setState({ productOption, optionError: false, isSoldOut: isSoldOut ? isSoldOut : false, ...productImage, variantImages })
       }

       getMultiImages = (dataMedia, selectedOptions) => {
              var variantImages = []
              try {
                     dataMedia.map((dVr) => {
                            if (dVr.alt) {
                                   const match = dVr.alt.match(/#colour_([a-z-]+)/i);
                                   if (match) {
                                          let extractedWord = match[1].replace(/-/g, ' ');
                                          const matchedColor = selectedOptions.find(color => color.toLowerCase() === extractedWord.toLowerCase());
                                          if (matchedColor) variantImages.push(dVr?.src)
                                   } else variantImages = []
                            }
                     })
                     return variantImages
              }
              catch (error) {
                     return variantImages
              }
       }


       onAddToCart = () => {
              try {

                     const { productDetails } = this.props
                     if (productDetails && Object.keys(productDetails).length) {
                            const { data } = productDetails
                            const { variants } = data
                            const { productOption } = this.state
                            var selectedOptions = []
                            if (productOption && productOption.length > 0) {
                                   productOption.map((option) => {
                                          if (!option.selected) {
                                                 this.setState({ optionError: true })
                                                 return
                                          }
                                          else if (option.availableVarient.length == 0) {
                                                 this.setState({ isSoldOut: true })
                                                 return
                                          }
                                          else selectedOptions.push(option.selected)
                                   })
                            }
                            if (variants.length == 1) {
                                   this.setState({ selectedVariant: variants[0] })
                                   this.addProductsToCart(variants[0])
                            }
                            else {
                                   const result = findAInB(selectedOptions, variants);
                                   if (result && result.length > 0) {
                                          this.setState({ selectedVariant: result[0] })
                                          this.addProductsToCart(result[0])
                                   }

                            }
                     }
              } catch (error) {
                     console.log('error', error)
              }
       }

       addProductsToCart = (variant) => {
              const { wall, tagshopProductDeatil } = this.props
              const { ThemeInfo } = wall
              const { quantity } = this.state
              const payload = new FormData()
              payload.append('id', variant.id)
              payload.append('quantity', quantity)
              addToCart(payload).then(res => {
                     try {
                            if (window.$('#cart-icon-bubble').length) window.$('#cart-icon-bubble').load(window.location.href + " #cart-icon-bubble");
                            if (window.$('cart-drawer').length) window.$('cart-drawer').removeClass("is-empty")
                            if (window.$('#shopify-section-cart-drawer').length) window.$('#shopify-section-cart-drawer').load(window.location.href + " #shopify-section-cart-drawer");
                            else if (window.$('cart-drawer').length) {
                                   window.$('cart-drawer').load(window.location.href + " #CartDrawer");
                            }
                            else if (window.$('#Cart-Drawer').length) {
                                   window.$('#Cart-Drawer').load(window.location.href + " .side-panel-inner");
                                   window.$('#cart-drawer-toggle').load(window.location.href + " #cart-drawer-toggle");

                            }
                     } catch (error) {
                            console.error("Error loading cart components:", error);
                     }
                     themePostTracking({ type: 1, action: 2, wall: wall?.ThemeInfo?.webId, product_id: tagshopProductDeatil.trackingData?.product_id, post: tagshopProductDeatil.trackingData?.post, owner: wall?.ThemeInfo?.ownerId, feed: tagshopProductDeatil.trackingData?.feed, currency: tagshopProductDeatil?.trackingData?.currency, price:variant.price / 100,quantity })
                     this.setState({ continueShopping: true })
              }).catch(err => {
                     this.setState({ continueShopping: true })
              })
       }

       getColorImage = (color) => {
              const { productDetails } = this.props
              if (productDetails && Object.keys(productDetails).length) {
                     const { data } = productDetails
                     if (color) {
                            const result = data.variants.find(vr => vr.options.includes(color))
                            return result.featured_image ? result.featured_image?.src : this.props.productImg
                     }
              }
       }

       getCurrentVarient = (optiValue) => {

              const { productDetails } = this.props
              if (productDetails && Object.keys(productDetails).length) {
                     const { data } = productDetails
                     if (optiValue) {
                            const result = data.variants.find(vr => vr.options.includes(optiValue))
                            return result.featured_image ? result.featured_image?.src : null
                     }
              }

       }



       getProductImage = () => {
              const { productDetails } = this.props
              const { productOption } = this.state
              if (productOption && productOption.length > 0) {
                     if (productDetails && Object.keys(productDetails).length) {
                            const { data } = productDetails

                            if (productOption[0].selected) {
                                   const result = data.variants.find(vr => vr.options.includes(productOption[0].selected))
                                   var variantImages = this.getMultiImages(data.media, [productOption[0].selected])
                                   if (variantImages?.length == 0) variantImages = data.images
                                   this.setState({ productImage: result.featured_image ? result.featured_image?.src : this.props.productImg, variantImages })
                            }
                            else { this.setState({ productImage: data.featured_image }) }
                     }
              }

       }
       toggleExpand = event => {
              const { isExpanded } = this.state;
              this.setState({ isExpanded: !isExpanded })
       }

       render() {
              const { closeCart, productDetails, productImg, tagshopProductDeatil, closePopUp } = this.props

              const { productImage, optionError, continueShopping, selectedVariant, productOption, isSoldOut, quantity, isExpanded, isReadMore, variantImages } = this.state
              const { price, discount, currency, title } = tagshopProductDeatil
              const discountPrice = Number(discount) > 0 && Number(discount) != Number(price) ? (((parseInt(price) - parseInt(discount)) / parseInt(price)) * 100).toFixed() : 0
              const postTextContent = isExpanded ? PostTrimContent(productDetails.data.description, trim_content_length) : productDetails.data.description

              return (
                     <div className="tb_cart_modal tb_modal_show" aria-hidden="true">
                            <div className="tb_cart_modal_dialog" role="document">
                                   <div className="tb_cart_modal_content">
                                          <div className="tb_c_close_btn" onClick={closePopUp}>
                                                 <div className='tb__icon tb-close-thin'><div></div></div>
                                          </div>
                                          <div className="tb_cart_modal_body">
                                                 {continueShopping ?
                                                        <ContinueShop productImg={productImg} productDetails={selectedVariant} tagshopProductDeatil={tagshopProductDeatil} closeAddtocart={closeCart} onBack={event => this.setState({ continueShopping: false })} currency={currency} discount={discount} price={price} discountPrice={discountPrice} productOption={productOption} />
                                                        :
                                                        <div className='tb_cart_container'>
                                                               <div className="tb_cart_back_btn" onClick={closeCart}>
                                                                      <div className='tb__icon tb-arrow-left'><div></div></div>
                                                                      Back
                                                               </div>
                                                               <div className="tb_cart_details">
                                                                      <div className="tb_cart_p_img tb_swiper">
                                                                             <Swiper autoHeight={true} pagination={{ clickable: true }} modules={[Pagination]} className="tb_cart_slider" key={variantImages.length}>
                                                                                    {
                                                                                    variantImages.length > 0 ? variantImages.map((imgItem) => <SwiperSlide><img src={imgItem ? imgItem : NO_PRODUCT} onError={e => this.setState({ productImage: NO_PRODUCT })} height={400} width={300} alt="" /></SwiperSlide>) : <SwiperSlide><img src={productImage ? productImage : NO_PRODUCT} onError={e => this.setState({ productImage: NO_PRODUCT })} height={400} width={300} alt="" /></SwiperSlide>
                                                                                    }      
                                                                             </Swiper>
                                                                      </div>
                                                                      <div className="tb_cart_p_details">
                                                                             <div className='tb_card_de_in'>
                                                                                    <div className='tb_cart_p_info'>
                                                                                           <div className="tb_cart_p_title">{title}</div>
                                                                                           <div className='tb_cart_pp_wrap'>
                                                                                                  <div className="tb_cart_p_price">{currency}{discount > 0 ? discount : price}</div>
                                                                                                  {discount > 0 && discount != price ? <div className="tb_cart_p_dis">{currency}{price}</div> : null}
                                                                                                  {discountPrice > 0 ? <div className="tb_cart_p_dp">{`${discountPrice}%`} off</div> : null}
                                                                                           </div>
                                                                                           {tagshopProductDeatil?.id?.length > 1 ? <div className="tb_cart_sku">SKU : {tagshopProductDeatil.id}</div> : null}
                                                                                           {productDetails?.data?.inventory_quantity?.length <= 4 ? <div className="tb_cart_s_l">Only {productDetails?.data?.inventory_quantity} left in stock</div> : null}
                                                                                           {!productDetails.data.available ? <div className="tb_cart_ofs">Out of Stock</div> : null}
                                                                                           {productDetails.data.description ? <><div className="tb_cart_p">{parse(postTextContent)}</div>

                                                                                                  {isReadMore ? <div className="tb_cart_read_more_" onClick={this.toggleExpand}>{isExpanded ? "Read More..." : "Show Less..."}
                                                                                                  </div> : null}

                                                                                           </> : null}
                                                                                           {productDetails.data.vendor ? <div className="tb_cart_s_v"><div className='tb_cart_s_vt'>Vendor: </div>{productDetails.data.vendor}</div> : null}
                                                                                    </div>
                                                                                    <div className='tb_cart_var_'>
                                                                                           {productOption && productOption.length > 0 ? <>
                                                                                                  {productOption.map((option, index) => {
                                                                                                         return <div className="tb_cart_size" key={index}>
                                                                                                                       <div className="tb_cart_s_label">{option.key}:</div>
                                                                                                                       {option.value.map((optValue, optindex) => {
                                                                                                                              const isMedia = this.getCurrentVarient(optValue)
                                                                                                                              const isAvailable = option.availableVarient.includes(optValue)
                                                                                                                              const selected = option.selected == optValue ? true : false
                                                                                                                              return <>
                                                                                                                                     {(String(option.key).toLowerCase().includes('size') || (String(option.key).toLowerCase().includes('color') && !option.isMediaShow) || isMedia == null) ?
                                                                                                                                            <div key={optindex} className={`tb_cart_s_btn${selected ? ' tb_active' : ''}${!isAvailable ? ' tb_disabled' : ''}`} onClick={()=>this.onClickSelectVarient(index, optValue, false)}>
                                                                                                                                                   {optValue}
                                                                                                                                            </div>
                                                                                                                                            :
                                                                                                                                     <div key={optindex} className={`tb_cart_color${selected ? ' tb_active' : ''}${!isAvailable ? ' ' : ''}`} onClick={()=>this.onClickSelectVarient(index, optValue, true)}>
                                                                                                                                            <div className="tb_cart_colorTile"><img src={isMedia} style={{ height: '100%', width: '100%' }} onError={e => e.target.src = NO_PRODUCT} /></div>
                                                                                                                                     </div>
                                                                                                                              }
                                                                                                                       </>
                                                                                                                })}
                                                                                                         </div>
                                                                                                  })}
                                                                                           </> : null

                                                                                           }
                                                                                    </div>
                                                                                    <div className="tb_cart_s_label">Quantity:</div>
                                                                                    <div className='tb__qu'>
                                                                                           <div className={`tb_qu_btn${quantity <= 1 ? ' tb_disabled' : ''}`} onClick={this.updateQuantity(false)}>-</div>
                                                                                           <div className='tb_qu_input'>{quantity}</div>
                                                                                           <div className={`tb_qu_btn${quantity >= 100 ? ' tb_disabled' : ''}`} onClick={this.updateQuantity(true)}>+</div>
                                                                                    </div>
                                                                                    {optionError ? <div className="tb_cart_p_error">Please select any one variant to proceed!</div> : <></>}
                                                                             </div>
                                                                             <div className="tb_cart_actions">
                                                                                    <div className="tb_cart_secondary" onClick={closeCart}>Cancel</div>
                                                                                    <div className="tb_cart_primary" style={isSoldOut ? { pointerEvents: 'none', opacity: 0.4 } : {}} onClick={this.onAddToCart}>ADD TO CART</div>
                                                                             </div>
                                                                      </div>
                                                               </div>
                                                        </div>
                                                 }
                                          </div>
                                   </div>
                            </div>
                     </div >

              )
       }
}


export default AddToCarts