import axios from "axios";
import { cartTrack, clientHost, magentoVariants } from "./api";

export const fetchShopifyProduct = (productName, isMagento, data) => 
    isMagento ? axios.post(magentoVariants, data) : axios.get(`${productName}.js`);

export const addToCart = (payload) => axios.post(`${clientHost}/cart/add.js`, payload);
export const getCartItemCount = () => axios.get(`${clientHost}/cart.js`);
export const trackAddToCart = (obj) => 
    axios.post(cartTrack, obj).catch(error => console.log("error", error));
